import {
    Tabs,
    TabsContent,
    TabsList,
    TabsTrigger,
  } from "@/components/ui/tabs";
  import {
    Card,
    CardContent,
  } from "@/components/ui/card";
  
  export default function GercabApp() {
    return (
      <main className="p-4 max-w-2xl mx-auto">
        <h1 className="text-2xl font-bold mb-4">GERCAB GO</h1>
        <Tabs defaultValue="absen" className="w-full">
          <TabsList>
            <TabsTrigger value="absen">Absen</TabsTrigger>
            <TabsTrigger value="daftar">Daftar</TabsTrigger>
          </TabsList>
          <TabsContent value="absen">
            <Card>
              <CardContent className="p-4">
                <p><form className="space-y-4 mt-4">
  <div className="grid gap-2">
    <label htmlFor="nama">Nama</label>
    <input
      type="text"
      id="nama"
      className="border px-3 py-2 rounded-md"
      placeholder="Masukkan nama"
    />
  </div>
  <div className="grid gap-2">
    <label htmlFor="kelas">Kelas</label>
    <input
      type="text"
      id="kelas"
      className="border px-3 py-2 rounded-md"
      placeholder="Masukkan kelas (misal: 7A)"
    />
  </div>
  <div className="grid gap-2">
    <label htmlFor="keterangan">Keterangan</label>
    <input
      type="text"
      id="keterangan"
      className="border px-3 py-2 rounded-md"
      placeholder="Misal: Hadir / Izin / Sakit"
    />
  </div>
  <button
    type="submit"
    className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-md"
  >
    Simpan Absen
  </button>
</form>
</p>
              </CardContent>
            </Card>
          </TabsContent>
          <TabsContent value="daftar">
            <Card>
              <CardContent className="p-4">
                <p>Data Daftar akan di sini</p>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </main>
    );
  }
  